import React, { Component } from "react";
import { connect } from "react-redux";
import { setLargeVideo } from "../../reducers/largeVideoReducer";
import { setStream } from "../../reducers/streamReducer";
import { store } from "../../redux/store";
import Video from "../Video/Video";

class LocalVideo extends Component {
  onVideoClick = (video) => {
    // this.props.onHandleSetLargeVideos();
    store.dispatch(setLargeVideo({ stream: video.srcObject }));
  };


  render() {
    return (
      <div style={{ width: "100%", height: "auto", overflow: "auto" }}>
        {this.props.localElementState.videos.map((video, index) => {
          return (
            <div
              key={index}
              style={{
                position: "relative",
                width: "100%",
                height: "auto",
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
              }}
            >
              <span
                style={{
                  color: "#ddd",
                  textShadow: "1px 1px black",
                  fontWeight: "bold",
                  position: "absolute",
                  maxWidth: "80%",
                  bottom: 5,
                }}
              >
                {window.roomClient?.username} (Me)
              </span>
              <Video srcObject={video?.stream} onClick={this.onVideoClick}></Video>
            </div>
          );
        })}

        {this.props.localElementState.screenCast.map((screen, index) => {
          return (
            <div
              key={index}
              style={{
                position: "relative",
                width: "100%",
                height: "auto",
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
              }}
            >
              <span
                style={{
                  color: "#ddd",
                  textShadow: "1px 1px black",
                  fontWeight: "bold",
                  position: "absolute",
                  maxWidth: "80%",
                  bottom: 5,
                }}
              >
                {window.roomClient?.username} (Me)
              </span>
              <Video srcObject={screen?.stream} onClick={this.onVideoClick}></Video>
            </div>
          );
        })}
      </div>
    );
  }
}
function mapStateToProps(state) {
  return {
    localElementState: state.localElementState,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    setLargeVideo: (data) => dispatch(setLargeVideo(data)),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(LocalVideo);
