import { Button, Dropdown } from "antd";
import React, { Component } from "react";
import { connect } from "react-redux";
import { Role } from "../../global/Role";
import { setViewOneCamera } from "../../reducers/uiReducer";
import MenuComponent from "../Menu/Menu";
import CameraOption from "../Option/CameraOption";
import MicOption from "../Option/MicOption";
import './style.scss'
class FooterTool extends Component {
          constructor(props) {
                    super(props);
                    this.state = { openCamera: Role.cameraEnable, openMic: Role.micEnable };
          }
          onChangeView = () => {
                    console.log("onChangeView");
                    this.props.setViewOneCamera()
          }
          render() {
                    const { userOnRoomState } = this.props
                    return (
                              <div
                                        className="footerTool"
                                        style={{
                                                  width: "100%",
                                                  height: "100%",
                                                  justifyContent: "center",
                                                  alignItems: "center",
                                                  display: "flex",
                                                  position: "relative"
                                        }}
                              >
                                        <div style={{ position: "absolute", left: 0, display: "flex" }}>
                                                  <Button style={{ margin: "0px 5px" }}
                                                            onClick={this.props.onDisplayChat}
                                                            size="large"
                                                            shape="circle"
                                                            icon={<i className="material-icons">chat</i>}>
                                                  </Button>
                                                  <Button style={{ margin: "0px 5px" }}
                                                            onClick={this.props.onDisplayRoomInfo}
                                                            size="large"
                                                            shape="circle"
                                                            icon={<i className="material-icons">security</i>}>
                                                  </Button>
                                                  <Dropdown overlay={<MenuComponent />} trigger={['click']}>
                                                            <Button style={{ margin: "0px 5px" }}
                                                                      // onClick={this.props.onDisplayChat}
                                                                      size="large"
                                                                      shape="circle"
                                                                      icon={<i className="material-icons">menu</i>}>
                                                            </Button>
                                                  </Dropdown>,

                                        </div>

                                        <Button
                                                  onClick={this.props.onStartScreenShare}
                                                  style={{ margin: "0px 5px" }}
                                                  size="large"
                                                  shape="circle"
                                                  icon={<i className="material-icons">tv</i>}
                                        >
                                        </Button>
                                        {this.state.openMic ? <div style={{ position: "relative" }} key="mic">
                                                  <Button
                                                            onClick={() => { this.props.onStopMic(); this.setState({ openMic: false }) }}
                                                            style={{ margin: "0px 5px" }}
                                                            size="large"
                                                            shape="circle"
                                                            icon={<i className="material-icons">mic</i>}
                                                  >
                                                  </Button>
                                                  <Dropdown forceRender={true} trigger={['click']} overlay={<MicOption />}>
                                                            <Button
                                                                      key="dasda"
                                                                      size="small"
                                                                      shape="circle"
                                                                      style={{ position: "absolute", bottom: -3, right: -3 }}
                                                                      icon={<i className="material-icons">expand_more</i>}
                                                            >
                                                            </Button>
                                                  </Dropdown>
                                        </div> : <Button
                                                  onClick={() => { this.props.onOpenMic(); this.setState({ openMic: true }) }}
                                                  style={{ margin: "0px 5px" }}
                                                  size="large"
                                                  shape="circle"
                                                  icon={<i className="material-icons">mic_off</i>}
                                        >
                                                  </Button>}


                                        <Button
                                                  style={{ margin: "0px 5px" }}
                                                  size="large"
                                                  type="primary"
                                                  shape="circle"
                                                  danger
                                                  icon={<i className="material-icons">call_end</i>}
                                                  onClick={this.props.onEndCall}
                                        >
                                        </Button>
                                        {this.state.openCamera ? (
                                                  <div style={{ position: "relative" }} key="camera">
                                                            <Button
                                                                      onClick={() => { this.props.onStopCamera(); this.setState({ openCamera: false }) }}
                                                                      style={{ margin: "0px 5px" }}
                                                                      size="large"
                                                                      shape="circle"
                                                                      icon={<i className="material-icons">videocam</i>}
                                                            >
                                                            </Button>
                                                            <Dropdown forceRender={true} trigger={['click']} overlay={<CameraOption />}>

                                                                      <Button
                                                                                size="small"
                                                                                shape="circle"
                                                                                style={{ position: "absolute", bottom: -3, right: -3 }}
                                                                                icon={<i className="material-icons">expand_more</i>}
                                                                      >
                                                                      </Button>
                                                            </Dropdown>
                                                  </div>
                                        ) : (
                                                            <Button
                                                                      onClick={() => { this.props.onOpenCamera(); this.setState({ openCamera: true }) }}
                                                                      style={{ margin: "0px 5px" }}
                                                                      size="large"
                                                                      shape="circle"
                                                                      icon={<i className="material-icons">videocam_off</i>}
                                                            >
                                                            </Button>
                                                  )}
                                        <div style={{ position: "absolute", right: 0, display: "flex" }}>
                                                  {userOnRoomState.length > 1 ? <Button style={{ margin: "0px 5px" }} size="large" onClick={this.onChangeView} shape="circle" icon={<i className="material-icons">grid_on</i>}>
                                                  </Button> : null}
                                                  <Button onClick={this.props.onShareLink} style={{ margin: "0px 5px" }} size="large" shape="circle" icon={<i className="material-icons">person_add</i>}>
                                                  </Button>
                                                  <Button onClick={this.props.onDisplayRemote} style={{ margin: "0px 5px" }} size="large" shape="circle" icon={<i className="material-icons">swap_horiz</i>}>
                                                  </Button>
                                        </div>

                              </div>
                    );
          }
}
function mapStateToProps(state) {
          return {
                    streamState: state.streamState,
                    userOnRoomState: state.userOnRoomState
          };
}

function mapDispatchToProps(dispatch) {
          return {
                    setViewOneCamera: () => dispatch(setViewOneCamera())
                    // setModal: (data) => dispatch(setModal(data)),
                    // setpdfViewer: (data) => dispatch(setpdfViewer(data)),
          };
}

export default connect(mapStateToProps, mapDispatchToProps)(FooterTool);
